    MyAppToolbarComponent.prototype.toastMsg = function (msg, controlToFocus, duration) {
        if (controlToFocus === void 0) { controlToFocus = null; }
        if (duration === void 0) { duration = -1; }
        if (this.toast != null)
            this.toast.dismiss();
        if (msg != "") {
            if (duration == -1) {
                this.toast = this.toastCtrl.create({
                    message: msg,
                    showCloseButton: true,
                    closeButtonText: 'OK',
                    position: 'bottom'
                });
            }
            else {
                this.toast = this.toastCtrl.create({
                    message: msg,
                    duration: duration,
                    position: 'bottom'
                });
            }
            this.toast.onDidDismiss(function () {
                /*if(controlToFocus!=null && controlToFocus!= undefined)
                 {
                 controlToFocus.setFocus();
                 /!*if(controlToFocus.nativeElement != undefined)
                 controlToFocus.nativeElement.focus();*!/
                 }*/
                //console.log('Dismissed toast');
            });
            this.toast.present();
            setTimeout(function () {
                if (controlToFocus != null && controlToFocus != undefined) {
                    controlToFocus.setFocus();
                }
            }, 1500);
        }
    };
