    MyAppToolbarComponent.prototype.addWalletAddress = function () {
        if (this.toast != null)
            this.toast.dismiss();
        if (this.addAddressForm.valid) {
            var data = this.id;
            if (!this.isValidWalletAddress(data, this.coinType)) {
                this.toastMsg(this.coinType + " address is invalid.", this.walletAddressInputBox, 3000);
                return;
            }
            //For testing (in Browser)
            // this.onAddSuccess(data);
            //Add to SQLLite
            this.insertInToDBTable(data);
            this.id = '';
            this.showError = false;
        }
        else {
            //this.toastMsg("Wallet address is required.",this.walletAddressInputBox);
            this.toastMsg("Wallet address is required.", this.walletAddressInputBox, 3000);
            this.showError = true;
        }
    };
    //QR Code scanner UI event
    MyAppToolbarComponent.prototype.scanbarcode = function () {
        var _this = this;
        this.barcodeScanner.scan({
            prompt: "Place a QR Code inside the scan area.",
            formats: "QR_CODE"
        }).then(function (barcodeData) {
            if (barcodeData.cancelled || barcodeData.text == "")
                return;
            // Success! QR code is here
            if (barcodeData.format == "QR_CODE") {
                var scanAddress = "";
                try {
                    var JSONObj = JSON.parse(barcodeData.text);
                    _this.id = JSONObj[0].toString();
                }
                catch (ex) {
                    scanAddress = barcodeData.text;
                }
                var pos = 0;
                if ((pos = scanAddress.indexOf(":")) != -1) {
                    _this.id = scanAddress.substr(pos + 1).trim();
                }
                else
                    _this.id = scanAddress.trim();
                _this.ChangeDetectorRef.detectChanges();
                _this.addWalletAddress();
            }
            else {
                //Display a toast as 'Not a valid QR code' with OK button.
                _this.toastMsg("Not a valid QR code.", null, 3000);
            }
        }, function (err) {
            //alert("error");
            // An error occurred
            _this.toastMsg("Something went wrong. Check your internet connection and try again.", null, 3000);
        });
    };
