    MyAppToolbarComponent.prototype.isValidWalletAddress = function (address, type) {
        var regexp;
        var isValid;
        switch (type) {
            case "BTC":
            case "BCH":
                regexp = new RegExp('^[13][a-km-zA-HJ-NP-Z1-9]{25,34}$');
                break;
            case "LTC":
                regexp = new RegExp('^[Ll3Mm][a-zA-Z0-9]{25,34}$');
                break;
            case "ETC":
            case "ETH":
                regexp = new RegExp('^0[xX][0-9a-fA-F]+$');
                if (address.substring(0, 2) != '0x' || address.length != 42) {
                    return false;
                }
                if (regexp.test(address)) {
                    return true;
                }
                else {
                    return false;
                }
            case "XRP":
                regexp = new RegExp("r[0-9a-zA-Z]{33}");
                isValid = regexp.test(address);
                return isValid;
            case "DASH":
                regexp = new RegExp("X[1-9A-HJ-NP-Za-km-z]{33}");
                isValid = regexp.test(address);
                return isValid;
            case "ZEC":
                if (address.charAt(0).toLowerCase() != "t") {
                    // not start with t
                    if (address.charAt(0).toLowerCase() != "z") {
                        // not start with z
                        return false;
                    }
                }
                try {
                    var checkzec = base58check.decode(address, 'hex');
                    return checkzec.prefix === '1c';
                }
                catch (error) {
                    return false;
                }
            case "DOGE":
                if (address.charAt(0).toUpperCase() != "D") {
                    // not start with D
                    if (address.charAt(0).toUpperCase() != "A") {
                        // not start with A
                        return false;
                    }
                }
                try {
                    var checkdoge = base58check.decode(address, 'hex');
                    if (checkdoge.prefix === '1e' || checkdoge.prefix === '16') {
                        return true;
                    }
                    else {
                        return false;
                    }
                }
                catch (error) {
                    return false;
                }
            case "WAVE":
                regexp = new RegExp("3P[0-9a-zA-Z]{33}");
                isValid = regexp.test(address);
                return isValid;
            case "DGB":
            case "PIVX":
            case "COLX":
                if (address.charAt(0) != "D") {
                    // not start with D
                    return false;
                }
                try {
                    var checkcolx = base58check.decode(address, 'hex');
                    return checkcolx.prefix === '1e';
                }
                catch (error) {
                    return false;
                }
            case "XVG":
                if (address.charAt(0) != "V") {
                    // not start with V
                    return false;
                }
                try {
                    var checkxvg = base58check.decode(address, 'hex');
                    return checkxvg.prefix === '46';
                }
                catch (error) {
                    return false;
                }
            case "PPC":
                if (address[0] != "P") {
                    // not start with P
                    return false;
                }
                try {
                    var checkppc = base58check.decode(address, 'hex');
                    return checkppc.prefix === '37';
                }
                catch (error) {
                    return false;
                }
            default:
                return true;
        }
        if (regexp.test(address)) {
            return true;
        }
        else {
            return false;
        }
    };
